<template>
  <div>
     <router-view v-if="edit" @editList="editList"></router-view>
     <div class="listBox" v-else>
        <div class='search'>
         <SearchList :options="options" @submit="searchList"/>
         <router-link :to="{name: 'ProductAdd'}" class="add">
           <a-button>添加商品</a-button>
         </router-link>
        </div>
        <div class='list'>
         <List :data="data"
         :page="page"
         @changePage="changePage"
         @removeProduct="removeProduct"
         />
        </div>
     </div>
  </div>
</template>

<script>
import SearchList from '@/components/searchList/index.vue';
import List from '@/components/list/index.vue';

export default {
  data() {
    return {
      options: [],
      data: [],
      page: {
        current: 1,
        pageSize: 10,
        showSizeChanger: true,
        total: 1,
      },
      searchForm: {},
      optionsObj: {},
    };
  },
  async created() {
    const optionData = await this.$api.catlist();
    this.getList();
    this.options = optionData.data.data;
    this.options.forEach((data) => {
      this.optionsObj[data.id] = data.name;
    });
  },
  components: {
    SearchList,
    List,
  },
  computed: {
    edit() {
      if (this.$route.params.id) {
        return true;
      }
      return false;
    },
  },
  methods: {
    async getList(page) {
      const params = {
        page: this.page.current,
        size: this.page.pageSize,
        ...this.searchForm,
        ...page,
      };
      const Data = await this.$api.prolist(params);
      this.data = Data.data.data.map((item) => ({
        ...item,
        categoryName: this.optionsObj[item.category],
      }));
      this.page.current = params.page;
      this.page.pageSize = params.size;
      this.page.total = Data.data.total;
    },

    async searchList(from) {
      this.searchForm = from;
      await this.getList({ page: 1, size: 10 });
      this.$message.success('搜索成功');
    },

    changePage(newPage) {
      this.page = newPage;
      this.getList();
    },

    async removeProduct(e) {
      this.$confirm({
        title: '确认删除',
        content: `是否删除${e.title}`,
        onOk: () => {
          this.$api.remove(e.id).then(() => {
            this.$message.success('删除成功');
            this.getList();
          });
        },
        onCancel: () => {
          console.log('cancel');
        },
      });
    },

    editList() {
      this.$message.success('编辑成功');
      this.getList();
    },
  },
};
</script>

<style scoped lang="less">
  .listBox{
    .search{
      padding: 10px 30px;
      .ant-form{
        display: inline-block;
      }
      .add{
        display: inline-block;
        line-height: 40px;
        float: right;
      }
    }
  }
</style>
