<template>
  <a-form layout="inline" @submit="handleSubmit">
    <a-form-item label="检索关键字">
      <a-input v-model="searchForm.searchWord" placeholder="请输入关键字" />
    </a-form-item>
    <a-form-item label="商品类目">
     <a-select
    show-search
    placeholder="请选择商品类目"
    style="width: 200px"
    @change="handleChange"
    allowClear
  >
    <a-select-option v-for="item in options" :key="item._id" :value="item.id">
      {{item.name}}
    </a-select-option>
  </a-select>
    </a-form-item>
    <a-form-item>
      <a-button type="primary" html-type="submit">
        搜索
      </a-button>
    </a-form-item>
  </a-form>
</template>

<script>
export default {
  data() {
    return {
      searchForm: {
        searchWord: '',
        category: '',
      },
    };
  },
  props: ['options'],
  methods: {
    handleSubmit() {
      this.$emit('submit', this.searchForm);
    },
    handleChange(value) {
      this.searchForm.category = value;
    },
  },
};
</script>
